.card {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 40rem;
  height: 60rem;
  margin: 20px;
  box-shadow: 50px 30px 20px rgba(0, 0, 0, 0.678);
  color: linear-gradient(to top, #a3bded 0%, #6991c7 100%);
  border: 3px solid rgba(0, 0, 0, 0.616);
  padding: 30px;
  border-radius: 20px;
  backdrop-filter: blur(2px);
  background-color: rgba(255, 255, 255, 0.2);
}

.source{
  text-decoration: none;
  color: teal;
}

.img{
  width: 70%;
  border-radius: 10px;
  box-shadow: 10px 5px 5px rgba(0, 0, 0, 0.678);

}